import { moment } from "moment";

export default {
  components: {
    Grid
    // Datepicker
  },
  data() {
    return {
      svcOprtrSeq: "",
      svcOprtrs: [],
      frnchsSeq: "",
      frnchss: [],

      date1: false,
      rgsDe1: new Date().toISOString().substr(0, 10),

      breadcrumbs: [
        {
          text: "Home",
          disabled: false,
          href: "/"
        },
        {
          text: "로그 현황",
          disabled: true,
          href: "/logs"
        },
        {
          text: "미사용 계정",
          disabled: true,
          href: "/logs/logs06List"
        }
      ],
    };
  },
  //   //  created:  function()  {
    created() {

      this.gridProps= {
        //  rowHeaders:  ["rowNum"],
        rowHeaders: [],
        columns: [
          { header: "순번", name: "rowNo", minWidth: 50, align: "left" },
          {
            header: "프랜차이즈",
            name: "frnchsNm",
            minWidth: 80,
            align: "left"
          },
          { header: "지사", name: "brffcNm", minWidth: 80, align: "left" },
          {
            header: "가맹점번호",
            name: "mrhstSeq",
            minWidth: 150,
            align: "left",
            hidden: true
          },
          { header: "가맹점", name: "mrhstNm", minWidth: 150, align: "left" },
          { header: "사용자 ID", name: "userId", minWidth: 150, align: "left" },
          { header: "사용자명", name: "userNm", minWidth: 100, align: "left" },
          {
            header: "전화번호",
            name: "reprsntTelNo",
            minWidth: 100,
            align: "left"
          },
          { header: "최근 접속일", name: "rgsDe", minWidth: 80, align: "left" },
          {
            header: "사용자권한",
            name: "authorCodeNm",
            minWidth: 100,
            align: "left"
          }
        ],
        //  data:  {},
        data: {
          api: {
            readData: { url: "/api/logs/logs06List/paging", method: "GET" }
          }, //  api
          initialRequest: false //  초기  렌더링  시  백엔드에  요청  X
        },
        ovsTheme: {
          name: "ovsTheme",
          value: {
            row: {
              even: {
                background: "#eeeeee"
              },
              hover: {
                background: "#ffffff"
              }
            },
            cell: {
              normal: {
