              },
              header: {
                background: "#eeeeee",
                border: "#aaaaaa"
              },
              editable: {
                border: "#777777"
              }
            }
          }
        },
        options: {
          scrollX: true,
          scrollY: false,
          rowHeight: 30,
          //  rowHeaders:  ["checkbox"]
          bodyHeight: 498, //  바디  Row  기본  높이는  27
          pagination: true,
          columnOptions: {
            frozenCount: 3,
            frozenBorderWidth: 2
          },
          pageOptions: {
            perPage: 12
          }
        }
      };

  //     axios.defaults.baseURL = document.location.origin;
  //     axios.defaults.headers.common[
  //       document.querySelector("meta[name='_csrf_token']").getAttribute("content")
  //     ] = document
  //       .querySelector("meta[name='_csrf_header']")
  //       .getAttribute("content");
  //     axios.defaults.headers.get["Accepts"] = "application/json";
