  //     axios
  //       .all([axios.get("/api/svcoprtr", { params: { svcOprtrSeq: 0 } })])
  //       .then(
  //         axios.spread(resp => {
  //           //  console.log(resp);
  //           this.svcOprtrs = resp.data.result;
  //         })
  //       );
    }, //  created
  methods: {
    getParams() {
      return {
        svcOprtrSeq: this.svcOprtrSeq,
        frnchsSeq: this.frnchsSeq,
        rgsDe1: moment(this.rgsDe1)
          .format("YYYY-MM-DD")
          .toString(),
      };
    },
    srhClicked(ev) {
      if (this.svcOprtrSeq != "" && this.frnchsSeq != "") {
        let params = this.getParams();
        console.log("readData  params  ::  ", params, ev);
        this.$refs.gLog06.invoke("readData", 1, params);
      } else {
        alert("서비스  운영사,  프랜차이즈는  반드시  선택해야  합니다.");
        return false;
      }
    },
    svcOprtrChanged(ev) {
      this.frnchss = [];
      this.brffcs = [];
      this.mrhsts = [];
      this.frnchsSeq = "";

      if (ev.target.value === "") {
        return false;
      }
      axios
        .get("/api/frnchsoprtr/allCode", {
          params: {
            svcOprtrSeq: this.svcOprtrSeq
          }
        })
        .then(resp => {
          //  console.log(resp);
          this.frnchss = resp.data.result;
        });
    },
    frnchsChanged(ev) {
      this.brffcs = [];
      this.mrhsts = [];
      //  console.log("frnchsChanged  event:  ",  ev);
      if (ev.target.value === "") {
        return false;
      }
      axios
        .get("/api/brffc/allCode", {
          params: {
            frnchsSeq: this.frnchsSeq
          }
        })
        .then(resp => {
          //  console.log(resp);
          this.brffcs = resp.data.result;
        });
    },
    viewDay({ date }) {
      this.start = date;
      this.type = "day";
    },
    getEventColor(event) {
      return event.color;
    },
    showIntervalLabel(interval) {
      return interval.minute === 0;
    },
    getEvents({ start, end }) {
      const events = [];
      const min = new Date(`${start.date}T00:00:00`);
      const max = new Date(`${end.date}T23:59:59`);
      const days = (max.getTime() - min.getTime()) / 86400000;
      const eventCount = this.rnd(days, days + 20);
      for (let i = 0; i < eventCount; i++) {
        const allDay = this.rnd(0, 3) === 0;
        const firstTimestamp = this.rnd(min.getTime(), max.getTime());
        const first = new Date(firstTimestamp - (firstTimestamp % 900000));
        const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000;
        const second = new Date(first.getTime() + secondTimestamp);
        events.push({
          name: this.names[this.rnd(0, this.names.length - 1)],
          start: this.formatDate(first, !allDay),
          end: this.formatDate(second, !allDay),
          color: this.colors[this.rnd(0, this.colors.length - 1)]
        });
      }
      this.events = events;
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    formatDate(a, withTime) {
      return withTime
        ? `${a.getFullYear()}-${a.getMonth() +
            1}-${a.getDate()}  ${a.getHours()}:${a.getMinutes()}`
        : `${a.getFullYear()}-${a.getMonth() + 1}-${a.getDate()}`;
    }
  } //  method
};
</script>

